	module.exports = {"data":{"tweetsJson":{"from":"Emily Webber","date":"5 Aug 2014","tags":"#agile #lean","text":"Terminology: Technical Debt","searchResults":[{"title":"Technical debt - Wikipedia","href":"https://en.wikipedia.org/wiki/Technical_debt","description":"Technical debt. Technical debt (also known as design debt or code debt) is a concept in software development that reflects the implied cost of additional rework caused by choosing an easy solution now instead of using a better approach that would take longer. Technical debt can be compared to monetary debt."},{"title":"technical debt - What is the standard terminology for the scenario ...","href":"https://softwareengineering.stackexchange.com/questions/330832/what-is-the-standard-terminology-for-the-scenario-of-the-agile-iterative-cliff","description":"1 Nov 2016 ... If that realization doesn't happen, the entire thing is usually called a death march, \nbut that is a rather generic term which encompasses various kinds of failures, not \njust technical debt. It describes a failed company/project/software where the \npeople in charge have yet to realize it failed, and generally try use the same ..."},{"title":"What is Technical Debt? - Definition from Techopedia","href":"https://www.techopedia.com/definition/27913/technical-debt","description":"The term was coined by Ward Cunningham, a programmer who is also known for \ndeveloping the first wiki.Technical debt is a metaphor that equates software \ndevelopment to financial debt. Imagine that you have a project that has two \npotential options. One is quick and easy but will require modification in the future."}],"index":370}},"pathContext":{"index":370}}

